/* global $ YT Swiper */

$(document).ready(() => {
    // Youtube video
    const tag = document.createElement('script');
    tag.src = 'https://www.youtube.com/player_api';
    const firstScriptTag = document.getElementsByTagName('script')[0];
    firstScriptTag.parentNode.insertBefore(tag, firstScriptTag);

    const iframes = $('.js-video');
    const players = [];

    window.onYouTubePlayerAPIReady = () => {
        for (let i = 0; i < iframes.length; i++) {
            const iframeItem = iframes[i];

            players[i] = new YT.Player(iframeItem, {
                suggestedQuality: 'hd720',
            });
        }
    };


    $('.video__backdrop').click(function videoBackdropClick() {
        const $this = $(this);
        $this.hide();

        const iframe = $this.siblings('.js-video');
        const i = iframes.index(iframe);

        if (players[i] && players[i].playVideo) {
            players[i].playVideo();
        }
    });


    const $window = $(window);
    const $html = $('html');
    const $body = $('body');
    const $container = $('.container');


    $('.js-scroll-to-top').on('click', () => {
        $html.animate({
            scrollTop: 0,
        }, 1000);
        return false;
    });

    // header

    const $header = $('.header');

    $body.scrollspy({
        target: '#header-nav',
        offset: 122,
    });

    $header.affix({
        offset: {
            top: 120,
        },
    });

    // $header.find('#header-nav a').click(function navClick(e) {
    //     const hash = this.hash;
    //     if (!hash) {
    //         return;
    //     }
    //
    //     e.preventDefault();
    //
    //     $html.animate({
    //         scrollTop: ($(hash).offset().top - 120),
    //     }, 1000, () => {
    //
    //         window.location.hash = hash;
    //         $html[0].scrollTop = ($(hash).offset().top - 120);
    //     });
    // });

    $('.js-mobile-nav-toggle').click(function () {
        $(this).toggleClass('_close');
        $header.toggleClass('_mobile-open');
        $body.toggleClass('modal-open');
    });

    $('.sidebar__close').click(function () {
        $(this)
            .closest('.sidebar')
            .toggleClass('_active');
        $body.toggleClass('_sidebar-active');
    });

    // tel mask

    $('.js-tel-mask').mask('(000) 000 00 00');


    // carouser = swiper

    const slidesOffsetBefore = () => ($window.width() - $container.width()) * 0.5;

    const bannerSwiper = new Swiper('#banner .swiper-container', {
        loop: true,
        preloadImages: false,
        lazy: true,
        navigation: {
            nextEl: '#banner .swiper-button._next',
            prevEl: '#banner .swiper-button._prev',
        },
    });

    const reviewsSwiper = new Swiper('#reviews .swiper-container', {
        loop: true,
        slidesPerView: 2,
        spaceBetween: 20,
        slidesOffsetBefore,
        slideToClickedSlide: true,
        preloadImages: false,
        lazy: {
            loadPrevNext: true,
        },
        navigation: {
            nextEl: '#reviews .swiper-button._next',
            prevEl: '#reviews .swiper-button._prev',
        },
        breakpoints: {
            1280: {
                slidesOffsetBefore: 0,
                slidesPerView: 1,
                spaceBetween: 0,
            },
        },
        on: {
            slideChange() {
                players.forEach(player => player.pauseVideo && player.pauseVideo());
            },
        },
    });

    const howSwiper = new Swiper('#how .swiper-container', {
        slidesPerView: 3,
        watchSlidesVisibility: true,
        // slidesOffsetBefore,
        slidesOffsetAfter: slidesOffsetBefore,
        navigation: {
            nextEl: '#how .swiper-button._next',
            prevEl: '#how .swiper-button._prev',
        },
        breakpoints: {
            1024: {
                // slidesPerView: 2,
            },
            768: {
                slidesPerView: 1,
            },
        },
    });

    const companySwiper = new Swiper('#company .swiper-container', {
        loop: true,
        slidesPerView: 4,
        spaceBetween: 50,
        watchSlidesVisibility: true,
        slidesOffsetBefore,
        preloadImages: false,
        lazy: true,
        navigation: {
            nextEl: '#company .swiper-button._next',
            prevEl: '#company .swiper-button._prev',
        },
        breakpoints: {
            1280: {
                spaceBetween: 20,
            },
            1024: {
                slidesPerView: 3,
            },
            768: {
                slidesPerView: 2,
            },
            480: {
                slidesPerView: 1,
                slidesOffsetBefore: 0,
            },
        },
    });

    const calcalateModalSwiper = new Swiper('#calculate-modal .swiper-container', {
        slidesPerView: 4,
        spaceBetween: 20,
        loop: true,
        slidesOffsetBefore,
        navigation: {
            nextEl: '#calculate-modal .swiper-button._next',
            prevEl: '#calculate-modal .swiper-button._prev',
        },
    });
});
